.genre__list
{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	margin-top: 2%;
	list-style: none;
}

.list__el
{
	width: 13.5%;
	margin: .5% 0 0 .5%;
	border: 1px solid #28262E;
	transition: .3s;
}

.list__link
{
	font-family: 'Lato', sans-serif;
	text-transform: uppercase;
	text-align: center;
	display: block;
	width: 100%;
	font-size: 1vw;
	padding: 11% 0 11% 0;
	color: #ADADAD;
	text-decoration: none;
}

.list__link:hover
{
	color: #fff;
}

.list__el:hover 
{
	background-color: #29272F;
}

 @media screen and (min-width: 764px) and (max-width: 1024px)
 {	

 }

 @media screen and (min-width: 375px) and (max-width: 764px)
 {
	.list__el
	{
		width: 13%;
		margin: .5% 0 0 .8%;
		border: 1px solid #28262E;
		transition: .3s;
	}
 }