
.main,
.filtered
{
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	width: 75%;
	padding-bottom: 5%;
	background-color: #1A191C;
}

.linkToTrailer
{
	text-decoration: none;
	color: #fff;
}

.trailerIdea
{
	text-align: center;
	word-wrap: break-word;
	width: 17.2vw;
	height: 30vw;
	margin: 15% 0 0 -2%;
	padding: 5% 2% 12% 2%;
	background-color: #1C1B20; 
	transition: .3s;
}

.trailerIdea:hover
{
	background-color: #312F38; 
}

.trailerIdea__img
{
	width: 17vw;
	height: 25vw;
}

.trailerIdea__title
{
	font-family: 'ZCOOL XiaoWei', serif;
	display: block;
	position: relative;
	margin-top: 7%;
	padding-bottom: 5%;
	overflow: hidden;
}

.trailerIdea__year
{
	font-family: 'ZCOOL XiaoWei', serif;
	float: right;
	color: #FF0000;
}

.pagination
{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	width: 100%;
	list-style: none;
}

.pagination li
{
	font-family: 'ZCOOL XiaoWei', serif;
	position: relative;
	margin-top: 7%;
	left: 20%;
	margin-left: 2%;
	color: #fff;
	cursor: pointer;
}


 @media screen and (min-width: 764px) and (max-width: 1024px)
 {	
	.trailerIdea__img
	{
		width: 20vw;
		height: 29vw;
	}
 }

 @media screen and (min-width: 375px) and (max-width: 764px)
 {
	.trailerIdea
	{
		width: 23vw;
		height: 37vw;
		margin: 15% 0 0 -2%;
		padding: 5% 2% 12% 2%;
	}

	.trailerIdea__img
	{
		width: 22.5vw;
		height: 33vw;
	}

 }
header
{
	width: 30%;
	margin: 5% 0 0 10%;
}

.header__link
{
	font-family: 'Lato', sans-serif;
	font-size: 3vw;
	text-decoration: none;
	color: #fff;
}
.trailerView
{
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	width: 75%;
	padding-bottom: 5%;
	left: 1%;
	margin: 10% 0 0 0;
}

.trailerView__img
{
	width: 25%;
	height: 22vw;
}

.trailerView__info
{
	font-family: 'ZCOOL XiaoWei', serif;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	width: 72%;
	margin-left: 3%;
	color: #fff;
}

.info__el
{
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	top: -5%;
	width: 100%;
	padding: 1% 0 1% 0;
	margin-top: 3%;
}

.grey
{
	background-color: #29272F;
}

.info__el__name
{
	display: block;
	width: 20%;
	font-size: 1.1vw;
}

.info__el__text
{
	display: block;
	position: relative;
	left: 10%;	
	width: 70%;
	font-size: 1.3vw;
}

.info__link
{
	color: #fff;
	text-decoration: none;
	border-bottom: 1px solid #fff;
}

.trailerView__desc
{
	font-family: 'ZCOOL XiaoWei', serif;
	margin: 5% 1% 0 1%;
}

.desc__main
{
	height: 13vw;
	overflow: hidden;
}

.desc__text,
.desc__more
{
	color: #A8A8A8;
	font-size: 1.3vw;
	z-index: 2;
}

.desc__more
{
	color: #fff;
	margin: 1% 3% 0 0;
	float: right;
	cursor: pointer;
}


 @media screen and (min-width: 764px) and (max-width: 1024px)
 {	
	.trailerView__img
	{
		width: 25%;
		height: 25vw;
	}

	.info__el__name
	{
		font-size: 1.3vw;
	}
 }

 @media screen and (min-width: 375px) and (max-width: 764px)
 {
 	.trailerView__img
	{
		width: 25%;
		height: 27vw;
	}	

	.info__el__name
	{
		font-size: 1.3vw;
	}
 }
.trailerSearch
{
	width: 40%;
	margin: 6% 0 0 15%;
}

.form__name
{
	font-family: 'Lato', sans-serif;
	font-size: 1.1vw;
	width: 68%;
	padding: 1% 0 1% 2%;
	background-color: transparent;
	border: 1px solid #28262E;
	outline: none;
	color: #fff;
}

.form__btn
{
	font-family: 'Lato', sans-serif;
	font-size: 1.1vw;
	width: 25%;
	padding: 1% 0 1% 0;
	margin-left: 1%;
	background-color: #28262E;
	border: none;
	outline: none;
	color: #fff;
	cursor: pointer;
}

 @media screen and (min-width: 764px) and (max-width: 1024px)
 {	

 }

 @media screen and (min-width: 375px) and (max-width: 764px)
 {
 	.form__name
 	{
 		font-size: 1.4vw;
 	}

 	.form__btn
 	{
 		font-size: 1.4vw;
 	}
 }
.genre__list
{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	margin-top: 2%;
	list-style: none;
}

.list__el
{
	width: 13.5%;
	margin: .5% 0 0 .5%;
	border: 1px solid #28262E;
	transition: .3s;
}

.list__link
{
	font-family: 'Lato', sans-serif;
	text-transform: uppercase;
	text-align: center;
	display: block;
	width: 100%;
	font-size: 1vw;
	padding: 11% 0 11% 0;
	color: #ADADAD;
	text-decoration: none;
}

.list__link:hover
{
	color: #fff;
}

.list__el:hover 
{
	background-color: #29272F;
}

 @media screen and (min-width: 764px) and (max-width: 1024px)
 {	

 }

 @media screen and (min-width: 375px) and (max-width: 764px)
 {
	.list__el
	{
		width: 13%;
		margin: .5% 0 0 .8%;
		border: 1px solid #28262E;
		transition: .3s;
	}
 }
