.trailerView
{
	position: relative;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 75%;
	padding-bottom: 5%;
	left: 1%;
	margin: 10% 0 0 0;
}

.trailerView__img
{
	width: 25%;
	height: 22vw;
}

.trailerView__info
{
	font-family: 'ZCOOL XiaoWei', serif;
	display: flex;
	flex-direction: column;
	width: 72%;
	margin-left: 3%;
	color: #fff;
}

.info__el
{
	position: relative;
	display: flex;
	flex-direction: row;
	top: -5%;
	width: 100%;
	padding: 1% 0 1% 0;
	margin-top: 3%;
}

.grey
{
	background-color: #29272F;
}

.info__el__name
{
	display: block;
	width: 20%;
	font-size: 1.1vw;
}

.info__el__text
{
	display: block;
	position: relative;
	left: 10%;	
	width: 70%;
	font-size: 1.3vw;
}

.info__link
{
	color: #fff;
	text-decoration: none;
	border-bottom: 1px solid #fff;
}

.trailerView__desc
{
	font-family: 'ZCOOL XiaoWei', serif;
	margin: 5% 1% 0 1%;
}

.desc__main
{
	height: 13vw;
	overflow: hidden;
}

.desc__text,
.desc__more
{
	color: #A8A8A8;
	font-size: 1.3vw;
	z-index: 2;
}

.desc__more
{
	color: #fff;
	margin: 1% 3% 0 0;
	float: right;
	cursor: pointer;
}


 @media screen and (min-width: 764px) and (max-width: 1024px)
 {	
	.trailerView__img
	{
		width: 25%;
		height: 25vw;
	}

	.info__el__name
	{
		font-size: 1.3vw;
	}
 }

 @media screen and (min-width: 375px) and (max-width: 764px)
 {
 	.trailerView__img
	{
		width: 25%;
		height: 27vw;
	}	

	.info__el__name
	{
		font-size: 1.3vw;
	}
 }