*
{
	margin: 0;
	padding: 0;
}

h4
{
	font-size: 1.2vw;
}

p
{
	font-size: 1.5vw;
}

body
{
	background-color: #000000;
}

.app
{
	background-color: #1A191C;
	width: 80%;
	margin: 15% 0 0 10%;
}

.header
{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

.genre
{
	width: 100%;
}

 @media screen and (min-width: 764px) and (max-width: 1024px)
 {	
	
	a
	{
		font-size: 1.2vw;
	}

	p
	{
		font-size: 1.6vw;
	}

	h4
	{
		font-size: 1.5vw;
	}

	.app
	{
		background-color: #1A191C;
		width: 90%;
		margin: 15% 0 0 5%;
	}
 }

 @media screen and (min-width: 375px) and (max-width: 764px)
 {
	
	a
	{
		font-size: 1.2vw;
	}

	p
	{
		font-size: 1.9vw;
	}

	h4
	{
		font-size: 1.9vw;
	}

	.app
	{
		background-color: #1A191C;
		width: 100%;
		margin: 15% 0 0 0;
	}
 }