.main,
.filtered
{
	position: relative;
	display: flex;
	justify-content: space-around;
	flex-direction: row;
	flex-wrap: wrap;
	width: 75%;
	padding-bottom: 5%;
	background-color: #1A191C;
}

.linkToTrailer
{
	text-decoration: none;
	color: #fff;
}

.trailerIdea
{
	text-align: center;
	word-wrap: break-word;
	width: 17.2vw;
	height: 30vw;
	margin: 15% 0 0 -2%;
	padding: 5% 2% 12% 2%;
	background-color: #1C1B20; 
	transition: .3s;
}

.trailerIdea:hover
{
	background-color: #312F38; 
}

.trailerIdea__img
{
	width: 17vw;
	height: 25vw;
}

.trailerIdea__title
{
	font-family: 'ZCOOL XiaoWei', serif;
	display: block;
	position: relative;
	margin-top: 7%;
	padding-bottom: 5%;
	overflow: hidden;
}

.trailerIdea__year
{
	font-family: 'ZCOOL XiaoWei', serif;
	float: right;
	color: #FF0000;
}

.pagination
{
	display: flex;
	flex-direction: row;
	width: 100%;
	list-style: none;
}

.pagination li
{
	font-family: 'ZCOOL XiaoWei', serif;
	position: relative;
	margin-top: 7%;
	left: 20%;
	margin-left: 2%;
	color: #fff;
	cursor: pointer;
}


 @media screen and (min-width: 764px) and (max-width: 1024px)
 {	
	.trailerIdea__img
	{
		width: 20vw;
		height: 29vw;
	}
 }

 @media screen and (min-width: 375px) and (max-width: 764px)
 {
	.trailerIdea
	{
		width: 23vw;
		height: 37vw;
		margin: 15% 0 0 -2%;
		padding: 5% 2% 12% 2%;
	}

	.trailerIdea__img
	{
		width: 22.5vw;
		height: 33vw;
	}

 }