.trailerSearch
{
	width: 40%;
	margin: 6% 0 0 15%;
}

.form__name
{
	font-family: 'Lato', sans-serif;
	font-size: 1.1vw;
	width: 68%;
	padding: 1% 0 1% 2%;
	background-color: transparent;
	border: 1px solid #28262E;
	outline: none;
	color: #fff;
}

.form__btn
{
	font-family: 'Lato', sans-serif;
	font-size: 1.1vw;
	width: 25%;
	padding: 1% 0 1% 0;
	margin-left: 1%;
	background-color: #28262E;
	border: none;
	outline: none;
	color: #fff;
	cursor: pointer;
}

 @media screen and (min-width: 764px) and (max-width: 1024px)
 {	

 }

 @media screen and (min-width: 375px) and (max-width: 764px)
 {
 	.form__name
 	{
 		font-size: 1.4vw;
 	}

 	.form__btn
 	{
 		font-size: 1.4vw;
 	}
 }